@use '../styles/variables' as *;
@use '../styles/mixins' as *;

// VerticalLayout Î©îÏù∏ Ïä§ÌÉÄÏùº
.vertical-layout {
  min-height: 100vh;
  padding: 0;
  width: 100%;
  overflow-x: hidden; // Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ

  &__container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background: rgba(255, 255, 255, 0.5);
    overflow-y: auto;
    overflow-x: hidden; // Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ
    position: relative;
    
    // Î™®Î∞îÏùº: Ï†ÑÏ≤¥ ÌôîÎ©¥ ÏÇ¨Ïö©, Îë•Í∏ÄÍ∏∞ ÏóÜÏùå
    margin: 0;
    border-radius: 0;
    box-shadow: none;
    
    // Îç∞Ïä§ÌÅ¨ÌÜ±: Ï§ëÏïô Ï†ïÎ†¨, ÏµúÎåÄ ÎÑàÎπÑ Ï†úÌïú, Í∑∏Î¶ºÏûê Ï∂îÍ∞Ä
    @include respond-to(md) {
      max-width: 448px;
      margin: 0 auto;
      border-radius: $border-radius-xl;
      box-shadow: $shadow-lg;
      max-height: 900px;
      height: 100vh;
    }
  }

  // Î∞∞Í≤Ω Ïû•Ïãù ÏöîÏÜå
  &__bg-decoration {
    position: absolute;
    width: 256px; // w-64 (16rem = 256px)
    height: 256px; // h-64
    background-color: $brand-brown;
    border-radius: 50%;
    opacity: 0.1;
    filter: blur(48px); // blur-3xl
    z-index: -1;
    
    &--top {
      top: 25%; // top-1/4
      left: -96px; // -left-24 (6rem = 96px)
    }
    
    &--bottom {
      bottom: 25%; // bottom-1/4
      right: -96px; // -right-24
    }
  }

  // Ìó§Îçî
  &__header {
    flex-shrink: 0;
    z-index: 10;
    position: relative;
    height: 120px; // Ìó§Îçî ÎÜíÏù¥ Îçî Ï∂ïÏÜå
  }
  
  &__header-image {
    position: absolute;
    inset: 0;
    width: 130%; // Ïù¥ÎØ∏ÏßÄ ÌôïÎåÄÌïòÏó¨ Ïò§Î•∏Ï™Ω Ïù¥Îèô Ìö®Í≥º
    height: 100%;
    object-fit: cover;
    object-position: right center; // Ïò§Î•∏Ï™Ω Ï†ïÎ†¨
    transform: translateX(60px); // Ïò§Î•∏Ï™ΩÏúºÎ°ú 60px Ïù¥Îèô
    z-index: 1;
    opacity: 0.8; // Ïù¥ÎØ∏ÏßÄÎ•º ÏÇ¥Ïßù Ìà¨Î™ÖÌïòÍ≤å
  }
  
  &__header-overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(to right, 
      rgba(247, 232, 211, 0.95) 0%,     // ÏôºÏ™Ω ÎÅù: Í±∞Ïùò Î∂àÌà¨Î™Ö
      rgba(247, 232, 211, 0.7) 30%,    // ÏôºÏ™Ω Ï§ëÍ∞Ñ: Î∞òÌà¨Î™Ö
      rgba(247, 232, 211, 0.3) 70%,    // Ïò§Î•∏Ï™Ω Ï§ëÍ∞Ñ: Ìà¨Î™Ö
      transparent 100%                  // Ïò§Î•∏Ï™Ω ÎÅù: ÏôÑÏ†Ñ Ìà¨Î™Ö (Ïù¥ÎØ∏ÏßÄ Ïûò Î≥¥ÏûÑ)
    );
    z-index: 2;
  }
  
  &__header-content {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: $spacing-sm $spacing-lg; // ÏÉÅÌïò Ìå®Îî© Ï§ÑÏûÑ
  }
  
  &__header-slogan {
    font-size: $font-sm;
    color: $gray-600;
    margin: 0 0 2px 0; // Í∞ÑÍ≤© ÎåÄÌè≠ Ï∂ïÏÜå
  }
  
  &__header-logo {
    margin-top: 2px; // Í∞ÑÍ≤© ÎåÄÌè≠ Ï∂ïÏÜå
  }
  
  &__header-logo-title {
    font-size: $font-xl;
    font-weight: 700;
    color: $brand-brown;
    margin: 0;
  }
  
  &__header-logo-subtitle {
    font-size: $font-xs;
    color: $gray-500;
    letter-spacing: 0.1em;
    margin: 0 0 2px 0; // ÌïòÎã® Í∞ÑÍ≤© Ï∂ïÏÜå
  }
  
  &__header-main-title {
    font-size: $font-3xl; // 4xlÏóêÏÑú 3xlÎ°ú (Ìè∞Ìä∏ ÌÅ¨Í∏∞ -1)
    font-weight: 500; // Î≥ºÎìú Ï†úÍ±∞ (900 ‚Üí 500)
    color: $gray-800;
    margin: 2px 0 0 0; // Í∞ÑÍ≤© Îçî Ï∂ïÏÜå
  }

  // Î©îÏù∏ ÏΩòÌÖêÏ∏†
  &__main {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: $spacing-sm $spacing-md $spacing-md;
    text-align: center;
    z-index: 10;
  }
}