@use '../styles/variables' as *;
@use '../styles/mixins' as *;

// Í≥µÏö© Card Ïª¥Ìè¨ÎÑåÌä∏ Ïä§ÌÉÄÏùº - ÏïÑÏù¥ÏΩò | ÌÅ∞Ï†úÎ™© + ÏûëÏùÄÏÑ§Î™Ö Î†àÏù¥ÏïÑÏõÉ
.card {
  background: $overlay-medium; // ÌÜ†ÌÅ∞ ÏÇ¨Ïö©ÏúºÎ°ú Îçî Î™ÖÌôïÌïú ÎåÄÎπÑ
  backdrop-filter: blur(16px);
  padding: $spacing-md;
  border-radius: $border-radius-xl;
  box-shadow: $shadow-md;
  border: 1px solid $gray-100;
  display: flex;
  flex-direction: column; // ÏÑ∏Î°ú Î∞∞ÏπòÎ°ú Î≥ÄÍ≤Ω
  justify-content: space-between; // ÏÉÅÌïò ÎÅùÏúºÎ°ú Ï†ïÎ†¨
  transition: box-shadow 0.3s ease;
  text-align: left;
  min-height: 140px; // ÏµúÏÜå ÎÜíÏù¥ Í≥†Ï†ï
  
  &:hover {
    box-shadow: $shadow-lg;
  }

  // ÌÅ¥Î¶≠ Í∞ÄÎä•Ìïú Ïπ¥Îìú Ïä§ÌÉÄÏùº
  &--clickable {
    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.3s ease;

    &:hover {
      transform: translateY(-2px);
      box-shadow: $shadow-lg;
    }

    &:active {
      transform: translateY(0);
    }

    &:focus {
      outline: 2px solid $brand-brown;
      outline-offset: 2px;
    }
  }
  
  // ÏïÑÏù¥ÏΩò Ïä§ÌÉÄÏùº - ÏôºÏ™Ω Í≥†Ï†ï
  &__icon {
    width: 48px;
    height: 48px;
    border-radius: $border-radius-lg;
    background-color: $brand-brown-light;
    color: $brand-brown;
    @include flex-center;
    flex-shrink: 0;
  }
  
  &__icon-svg {
    width: 28px;
    height: 28px;
    color: $brand-brown;
  }
  
  &__icon-text {
    font-size: 18px;
    font-weight: 700;
    color: $brand-brown;
    line-height: 1;
  }
  
  // Î©îÏù∏ ÏΩòÌÖêÏ∏† ÏòÅÏó≠ - ÏÉÅÎã® (ÏïÑÏù¥ÏΩò + ÌÖçÏä§Ìä∏)
  &__main-content {
    display: flex;
    align-items: flex-start;
    gap: $spacing-md;
    flex: 1;
  }
  
  // ÏΩòÌÖêÏ∏† ÏòÅÏó≠ - ÌÖçÏä§Ìä∏ Î∂ÄÎ∂Ñ
  &__content {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: $spacing-xs;
  }
  
  // ÌÅ∞Ï†úÎ™© Ïä§ÌÉÄÏùº
  &__title {
    font-size: $font-base; // 16px Í∏∞Î≥∏ ÌÖçÏä§Ìä∏ ÌÅ¨Í∏∞
    font-weight: 700;
    color: $gray-800;
    margin: 0;
    line-height: 1.2;
  }
  
  // ÏûëÏùÄÏÑ§Î™Ö Ïä§ÌÉÄÏùº
  &__description {
    font-size: $font-sm; // $font-xsÏóêÏÑú 1Îã®Í≥Ñ Ï¶ùÍ∞Ä
    color: $gray-600;
    line-height: 1.6;
    margin: 0;
    
    strong {
      color: $brand-brown;
      font-weight: 600;
    }
  }
  
  // Î∞îÎ°úÍ∞ÄÍ∏∞ ÏòÅÏó≠ - ÌïòÎã® Í≥†Ï†ï
  &__shortcut {
    margin-top: $spacing-md;
    padding-top: $spacing-xs;
    padding-right: $spacing-sm; // Ïò§Î•∏Ï™Ω Ìå®Îî©ÏúºÎ°ú Ï°∞Í∏à ÏôºÏ™ΩÏúºÎ°ú Ïù¥Îèô
    border-top: 1px solid $gray-300; // Ï°∞Í∏à Îçî ÏßÑÌïú Í∞ÄÎ°úÏ§Ñ
    min-height: 24px; // Í≥†Ï†ï ÎÜíÏù¥
    display: flex;
    align-items: center;
    justify-content: flex-end; // Ïò§Î•∏Ï™Ω Ï†ïÎ†¨ Ïú†ÏßÄ
    flex-shrink: 0; // Ï∂ïÏÜå Î∞©ÏßÄ
  }
  
  &__shortcut-text {
    font-size: $font-sm; // $font-xsÏóêÏÑú 1Îã®Í≥Ñ Ï¶ùÍ∞Ä
    color: $brand-brown;
    font-weight: 600;
    cursor: pointer;
    text-decoration: none;
    transition: all 0.2s ease;
    
    &:hover {
      color: $brand-brown-hover;
      text-decoration: underline;
    }
    
    // Î∞îÎ°úÍ∞ÄÍ∏∞ ÏïÑÏù¥ÏΩò Ï∂îÍ∞Ä
    &::before {
      content: "‚Üí ";
      margin-right: 2px;
      font-weight: 700;
    }
  }
  
  // ÏÑ∏Î°úÌòï ÌÉÄÏûÖ (Í∏∞Î≥∏ Î†àÏù¥ÏïÑÏõÉ Ïú†ÏßÄ)
  &--vertical {
    width: 100%;
  }
  
  // Í∞ÄÎ°úÌòï ÌÉÄÏûÖ (Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§Ïö© Í≥†Ï†ï ÎÑàÎπÑ) - Í∏∞Ï°¥ ÎåÄÎπÑ 10% ÌôïÎåÄ
  &--horizontal {
    // Î™®Î∞îÏùº: ÌôîÎ©¥Ïùò 73% (66%ÏóêÏÑú 10% Ï¶ùÍ∞Ä)
    width: calc(73vw - #{$spacing-md * 2});
    min-width: 231px; // 210pxÏóêÏÑú 10% Ï¶ùÍ∞Ä
    max-width: 317px; // 288pxÏóêÏÑú 10% Ï¶ùÍ∞Ä
    flex-shrink: 0;
    scroll-snap-align: start;
    
    // Ïä§ÌÅ¨Î°§ ÏÑ±Îä• Í∞úÏÑ†
    will-change: transform;
    transform: translateZ(0); // GPU Í∞ÄÏÜç ÌôúÏÑ±Ìôî
    
    // Í∞ÄÎ°úÌòï Ïπ¥ÎìúÏùò ÏïÑÏù¥ÏΩò ÌÅ¨Í∏∞ Ï°∞Ï†ï
    .card__icon {
      width: 36px;
      height: 36px;
      font-size: 16px; // ÏïÑÏù¥ÏΩò ÎÇ¥Î∂Ä ÌÖçÏä§Ìä∏/Ïù¥ÎØ∏ÏßÄ ÌÅ¨Í∏∞ÎèÑ Ï°∞Ï†ï
    }
    
    // Îç∞Ïä§ÌÅ¨ÌÜ±ÏóêÏÑúÎäî Í≥†Ï†ï ÎÑàÎπÑ (10% Ï¶ùÍ∞Ä)
    @include respond-to(md) {
      width: 277px; // 252pxÏóêÏÑú 10% Ï¶ùÍ∞Ä
    }
  }
}

// Ïπ¥Îìú Ïª®ÌÖåÏù¥ÎÑàÎ≥Ñ Ïä§ÌÉÄÏùº

// ÏÑ∏Î°úÌòï Ïπ¥ÎìúÎì§ Ïª®ÌÖåÏù¥ÎÑà
.cards-vertical {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
  width: 100%;
}

// Í∞ÄÎ°úÌòï Ïπ¥ÎìúÎì§ Ïª®ÌÖåÏù¥ÎÑà
.cards-horizontal {
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
  gap: $spacing-sm;
  padding: $spacing-sm calc((100vw - 75vw) / 2 + #{$spacing-md}) $spacing-lg calc((100vw - 75vw) / 2 + #{$spacing-md});
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  
  // Ïä§ÌÅ¨Î°§ ÏÑ±Îä• ÏµúÏ†ÅÌôî
  will-change: scroll-position;
  contain: layout style paint;
  
  // Îç∞Ïä§ÌÅ¨ÌÜ±ÏóêÏÑúÎäî Í∏∞Î≥∏ Ìå®Îî©ÏúºÎ°ú Î≥ÄÍ≤Ω
  @include respond-to(md) {
    padding: $spacing-sm $spacing-md $spacing-lg $spacing-md;
  }
  
  // Ïä§ÌÅ¨Î°§Î∞î Ïà®Í∏∞Í∏∞
  &::-webkit-scrollbar {
    display: none;
  }
  scrollbar-width: none;
  
  // Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ ÌûåÌä∏ Í∑∏ÎùºÎç∞Ïù¥ÏÖò Ï†úÍ±∞ (ÍπîÎÅîÌïú UI)
}

// Ïä§ÏôÄÏù¥ÌîÑ ÏòÅÏó≠ (Í∞ÄÎ°úÌòï Ï†ÑÏö©)
.swipe-area {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm; // ÌûåÌä∏ÏôÄ Ïπ¥Îìú Í∞ÑÍ≤© Ï§ÑÏûÑ
  margin-bottom: $spacing-md; // Ìë∏ÌÑ∞ÏôÄÏùò Í∞ÑÍ≤© Ï§ÑÏûÑ
  
  &__hint {
    text-align: center;
    font-size: $font-xs;
    color: $gray-500;
    margin-top: 0; // Ïπ¥ÎìúÏôÄÏùò Í∞ÑÍ≤© ÏôÑÏ†Ñ Ï†úÍ±∞
    padding: 0;
    line-height: 1.2;
    
    // Ïπ¥Îìú ÏúÑÏóê ÏúÑÏπòÌïòÎäî ÌûåÌä∏ (ÏÉàÎ°úÏö¥ ÏúÑÏπò)
    &--top {
      margin-bottom: $spacing-sm; // Ïπ¥ÎìúÏôÄÏùò Í∞ÑÍ≤©
      margin-top: $spacing-lg; // ÏÑúÎ∏åÌÉÄÏù¥ÌãÄÍ≥ºÏùò Í∞ÑÍ≤© ÎäòÎ¶º
      color: $gray-600; // Ï°∞Í∏à Îçî ÏßÑÌïú ÏÉâÏÉÅÏúºÎ°ú Í∞ïÏ°∞
      font-weight: 500; // ÏïΩÍ∞Ñ ÎëêÍªçÍ≤å
    }
  }
}
