@use 'variables' as *;

// Î∞òÏùëÌòï ÎØπÏä§Ïù∏
@mixin respond-to($breakpoint) {
  @if $breakpoint == sm {
    @media (min-width: $breakpoint-sm) {
      @content;
    }
  }
  @if $breakpoint == md {
    @media (min-width: $breakpoint-md) {
      @content;
    }
  }
  @if $breakpoint == lg {
    @media (min-width: $breakpoint-lg) {
      @content;
    }
  }
  @if $breakpoint == xl {
    @media (min-width: $breakpoint-xl) {
      @content;
    }
  }
}

// ÌîåÎ†âÏä§ Ï§ëÏïô Ï†ïÎ†¨
@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

// ÌîåÎ†âÏä§ Ïª¨Îüº Ï§ëÏïô Ï†ïÎ†¨
@mixin flex-column-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

// Î≤ÑÌäº Í∏∞Î≥∏ Ïä§ÌÉÄÏùº
@mixin button-base {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: $spacing-md $spacing-lg;
  border: none;
  border-radius: $border-radius-lg;
  font-weight: 500;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

// Î∏åÎûúÎìú Î≤ÑÌäº
@mixin button-brand {
  @include button-base;
  background-color: $brand-brown;
  color: $white;
  
  &:hover:not(:disabled) {
    background-color: $brand-brown-hover;
  }
}

// Ïπ¥Îìú Ïä§ÌÉÄÏùº
@mixin card {
  background: $white;
  border-radius: $border-radius-xl;
  box-shadow: $shadow-md;
  padding: $spacing-lg;
}

// ÌÖçÏä§Ìä∏ Ï§ÑÏûÑ
@mixin text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// Ïó¨Îü¨ Ï§Ñ ÌÖçÏä§Ìä∏ Ï§ÑÏûÑ
@mixin text-ellipsis-multiline($lines: 2) {
  display: -webkit-box;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
