@use 'variables' as *;

// Í∏∞Î≥∏ Ïä§ÌÉÄÏùº Î¶¨ÏÖã
* {
  box-sizing: border-box;
}

html, body {
  margin: 0 !important;
  padding: 0 !important;
  height: 100%;
  width: 100%;
  overflow-x: hidden; // Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ
  position: relative;
  
  // Î™®Î∞îÏùº Î∏åÎùºÏö∞Ï†Ä ÏÉÅÎã® Í≥µÍ∞Ñ ÏôÑÏ†Ñ Ï†úÍ±∞
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $background-beige;
  color: $gray-800;
  line-height: 1.6;
}

// Î™®Îì† ÏöîÏÜåÏóê ÌîÑÎ¶¨ÌÖêÎã§Îìú Ìè∞Ìä∏ Í∞ïÏ†ú Ï†ÅÏö©
*, *::before, *::after {
  font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif !important;
}

#root {
  min-height: 100vh;
  margin: 0 !important;
  padding: 0 !important;
  position: relative;
  
  // ÏÉÅÎã® Í≥µÍ∞Ñ ÏôÑÏ†Ñ Ï†úÍ±∞
  &::before {
    display: none !important;
  }
}

// ÎßÅÌÅ¨ Í∏∞Î≥∏ Ïä§ÌÉÄÏùº
a {
  color: $brand-brown;
  text-decoration: none;
  
  &:hover {
    text-decoration: underline;
  }
}

// Î≤ÑÌäº Í∏∞Î≥∏ Ïä§ÌÉÄÏùº
button {
  font-family: inherit;
}

// ÏûÖÎ†• ÌïÑÎìú Í∏∞Î≥∏ Ïä§ÌÉÄÏùº
input, textarea, select {
  font-family: inherit;
}

// Ïù¥ÎØ∏ÏßÄ Î∞òÏùëÌòï
img {
  max-width: 100%;
  height: auto;
}

// Ïä§ÌÅ¨Î°§Î∞î Ïä§ÌÉÄÏùº (webkit Î∏åÎùºÏö∞Ï†Ä)
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: $gray-100;
}

::-webkit-scrollbar-thumb {
  background: $gray-400;
  border-radius: 4px;
  
  &:hover {
    background: $gray-500;
  }
}
