@use './styles/variables' as *;
@use './styles/mixins' as *;

// App Î°úÎî© Ïä§ÌÉÄÏùº
.app-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: $background-beige;
  
  .loading-spinner {
    padding: $spacing-lg;
    font-size: $font-lg;
    color: $gray-600;
    text-align: center;
    
    &::after {
      content: '';
      display: inline-block;
      width: 20px;
      height: 20px;
      margin-left: $spacing-sm;
      border: 2px solid transparent;
      border-top: 2px solid $brand-brown;
      border-radius: 50%;
      animation: spin 1s linear infinite;
      vertical-align: middle;
    }
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

// ÌååÎπÑÏΩò Î∏îÎßÅÌÅ¨ Ïï†ÎãàÎ©îÏù¥ÏÖò
@keyframes faviconBlink {
  0%, 100% { 
    opacity: 1; 
    transform: scale(1);
  }
  50% { 
    opacity: 0.3; 
    transform: scale(0.95);
  }
}

.favicon-blink-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
}

// Ïï± Ïä§ÌÉÄÏùº
.app {
  min-height: 100vh;
  padding: 0;
  margin: 0;
  width: 100%;
  overflow-x: hidden; // Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ
  position: relative;
  
  // ÏÉÅÎã® Ïó¨Î∞± ÏôÑÏ†Ñ Ï†úÍ±∞
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 0;
    margin: 0;
    padding: 0;
  }
}

// Î©îÏù∏ Ïª®ÌÖåÏù¥ÎÑà
.main-container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.5);
  overflow-y: auto;
  overflow-x: hidden; // Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ
  position: relative;
  
  // Î™®Î∞îÏùº: Ï†ÑÏ≤¥ ÌôîÎ©¥ ÏÇ¨Ïö©, Îë•Í∏ÄÍ∏∞ ÏóÜÏùå
  margin: 0;
  border-radius: 0;
  box-shadow: none;
  
  // Îç∞Ïä§ÌÅ¨ÌÜ±: Ï§ëÏïô Ï†ïÎ†¨, ÏµúÎåÄ ÎÑàÎπÑ Ï†úÌïú, Í∑∏Î¶ºÏûê Ï∂îÍ∞Ä
  @include respond-to(md) {
    max-width: 448px;
    margin: 0 auto;
    border-radius: $border-radius-xl;
    box-shadow: $shadow-lg;
    max-height: 900px;
    height: 100vh;
  }
}

// Î∞∞Í≤Ω Ïû•Ïãù ÏöîÏÜå
.bg-decoration {
  position: absolute;
  width: 256px; // w-64 (16rem = 256px)
  height: 256px; // h-64
  background-color: $brand-brown;
  border-radius: 50%;
  opacity: 0.1;
  filter: blur(48px); // blur-3xl
  z-index: -1;
  
  &--top {
    top: 25%; // top-1/4
    left: -96px; // -left-24 (6rem = 96px)
  }
  
  &--bottom {
    bottom: 25%; // bottom-1/4
    right: -96px; // -right-24
  }
}

// Ìó§Îçî
.header {
  flex-shrink: 0;
  z-index: 10;
  position: relative;
  height: 208px; // h-52 (13rem = 208px)
  
  &__image {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
  
  &__overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(to right, $background-beige, rgba(247, 232, 211, 0.8), transparent);
  }
  
  &__content {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: $spacing-md $spacing-lg;
  }
  
  &__slogan {
    font-size: $font-sm;
    color: $gray-600;
    margin: 0 0 $spacing-xs 0;
  }
  
  &__logo {
    margin-top: $spacing-xs;
    
    &-title {
      font-size: $font-xl;
      font-weight: 700;
      color: $brand-brown;
      margin: 0;
    }
    
    &-subtitle {
      font-size: $font-xs;
      color: $gray-500;
      letter-spacing: 0.1em;
      margin: 0;
    }
  }
  
  &__main-title {
    font-size: $font-4xl;
    font-weight: 900;
    color: $gray-800;
    margin: $spacing-md 0 0 0;
  }
}

// Î©îÏù∏ ÏΩòÌÖêÏ∏†
.main-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: $spacing-sm $spacing-md $spacing-md;
  text-align: center;
  z-index: 10;
}

// ÌÉÄÏù¥ÌãÄ ÏÑπÏÖò
.title-section {
  flex-shrink: 0;
  margin-bottom: $spacing-md;
  
  &__title {
    font-size: $font-2xl;
    font-weight: 700;
    color: $gray-800;
    line-height: 1.25;
    margin: 0 0 $spacing-xs 0;
  }
  
  &__subtitle {
    font-size: $font-sm;
    color: $gray-500;
    margin: 0;
  }
}

// AI ÏÑπÏÖò
.ai-section {
  margin: $spacing-md 0;
  @include flex-center; // Ï§ëÏïô Ï†ïÎ†¨
}

// Ïπ¥Îìú ÏÑπÏÖò Ïä§ÌÉÄÏùºÏùÄ Card.scssÎ°ú Ïù¥ÎèôÎê®

// Í±¥Í∞ï Ïπ¥Îìú
.health-card {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(16px);
  padding: $spacing-md;
  border-radius: $border-radius-xl;
  box-shadow: $shadow-md;
  border: 1px solid $gray-100;
  border-left: 4px solid $brand-brown;
  display: flex;
  align-items: center;
  gap: $spacing-md;
  transition: box-shadow 0.3s ease;
  text-align: left;
  
  &:hover {
    box-shadow: $shadow-lg;
  }
  
  &__icon {
    width: 48px; // w-12 (3rem = 48px)
    height: 48px; // h-12
    border-radius: $border-radius-lg;
    background-color: $brand-brown-light;
    color: $brand-brown;
    flex-shrink: 0;
    @include flex-center;
    
    .icon {
      width: 28px; // w-7 (1.75rem = 28px)
      height: 28px; // h-7
    }
  }
  
  &__content {
    flex: 1;
  }
  
  &__title {
    font-size: $font-sm;
    font-weight: 700;
    color: $gray-800;
    margin: 0 0 $spacing-xs 0;
  }
  
  &__description {
    font-size: $font-xs;
    color: $gray-600;
    margin: 0;
    
    strong {
      color: $brand-brown;
      font-weight: 600;
    }
  }
}

// ÌïòÎã® ÏÑπÏÖò
.footer-section {
  flex-shrink: 0;
  margin-top: auto;
  
  &__message {
    font-size: 10px; // Ìè∞Ìä∏ ÌÅ¨Í∏∞ ÎåÄÌè≠ Ï∂ïÏÜå
    color: $gray-600;
    font-weight: 500;
    margin: 0 0 $spacing-xs 0; // ÌïòÎã® Ïó¨Î∞± Ï∂ïÏÜå
  }
  
  &__info {
    font-size: 9px; // Ï†ïÎ≥¥ ÌÖçÏä§Ìä∏ Îçî ÏûëÍ≤å
    color: $gray-500;
    margin-bottom: $spacing-xs; // ÌïòÎã® Ïó¨Î∞± Ï∂ïÏÜå
    
    p {
      margin: 0;
    }
  }
  
  &__spacing {
    height: 80px; // ÌîåÎ°úÌåÖ Î≤ÑÌäºÏùÑ ÏúÑÌïú Ïó¨Î∞±
  }
  
  // Í∞ÄÎ°úÌòï Î†àÏù¥ÏïÑÏõÉ Ï†ÑÏö© Ï§ëÍ∞ÑÏ†ïÎ†¨
  &--horizontal {
    text-align: center;
    
    .footer-section__message {
      text-align: center;
    }
    
    .footer-section__info {
      text-align: center;
    }
  }
  
  // Í∞ÄÎ°úÌòï Î†àÏù¥ÏïÑÏõÉÏóêÏÑú Ìë∏ÌÑ∞ Í∞ÑÍ≤© Ï∂ïÏÜå
  &--compact {
    margin-top: -$spacing-lg; // ÏúÑÏ™Ω Ïó¨Î∞±ÏùÑ ÏùåÏàòÎ°ú ÏÑ§Ï†ïÌïòÏó¨ Îçî ÏúÑÎ°ú
    
    .footer-section__message {
      margin-bottom: $spacing-xs; // Î©îÏãúÏßÄ ÌïòÎã® Ïó¨Î∞± Ï∂ïÏÜå (sm ‚Üí xs)
    }
    
    .footer-section__info {
      margin-bottom: $spacing-xs; // Ï†ïÎ≥¥ ÌïòÎã® Ïó¨Î∞± Ï∂ïÏÜå (sm ‚Üí xs)
    }
  }
}

// Í∞ÄÎ°úÌòï Î†àÏù¥ÏïÑÏõÉ Ìó§Îçî Ïä§ÌÉÄÏùº
.horizontal-cards {
  &__title {
    font-size: $font-2xl;
    font-weight: 700;
    color: $gray-800;
    line-height: 1.2;
    text-align: center; // Ï§ëÍ∞Ñ Ï†ïÎ†¨
    margin: $spacing-md 0 $spacing-lg 0; // Í∞ÑÍ≤© ÎäòÎ¶º
    
    // ÌôòÏûêÎ™Ö Í∞ïÏ°∞ Ïä§ÌÉÄÏùº
    .patient-name {
      font-size: $font-3xl; // ÌôòÏûêÎ™ÖÎßå Îçî ÌÅ¨Í≤å
      font-weight: 800;
      color: $gray-900; // Ï°∞Í∏à Îçî ÏßÑÌïú ÏÉâÏÉÅ
    }
  }
  
  &__subtitle {
    font-size: $font-sm;
    color: $gray-600;
    line-height: 1.6;
    text-align: center; // Ï§ëÍ∞Ñ Ï†ïÎ†¨
    margin: 0 0 $spacing-xl 0; // ÌïòÎã® ÎßàÏßÑ ÎäòÎ¶º
    
    // Î≥ëÏõêÎ™Ö Í∞ïÏ°∞ Ïä§ÌÉÄÏùº
    .hospital-name {
      color: $brand-brown;
      font-size: calc(#{$font-sm} + 1px); // 1px Îçî ÌÅ¨Í≤å
      font-weight: 600;
    }
  }
}

// Í∞ÄÎ°úÌòï Î©îÏãúÏßÄ ÏÑπÏÖò (Ïπ¥Îìú ÏïÑÎûò ÏòÅÏó≠)
.horizontal-message-section {
  margin: $spacing-lg 0;
  padding: $spacing-md;
  text-align: center;
  
  &__text {
    font-size: $font-sm;
    color: $gray-700;
    font-weight: 500;
    line-height: 1.6;
    margin: 0;
    
    // Ï≤´ Î≤àÏß∏ Ï§Ñ Í∞ïÏ°∞
    &::first-line {
      font-weight: 600;
      color: $gray-800;
    }
  }
}

// ÌîåÎ°úÌåÖ Î≤ÑÌäº Ïª®ÌÖåÏù¥ÎÑà (Î©îÏù∏ Ïª®ÌÖåÏù¥ÎÑà ÎÇ¥Î∂ÄÏóê ÏúÑÏπò)
.floating-button-container {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 448px; // Î©îÏù∏ Ïª®ÌÖåÏù¥ÎÑàÏôÄ ÎèôÏùºÌïú ÏµúÎåÄ ÎÑàÎπÑ
  background: rgba(255, 255, 255, 0.25); // Ïª®ÌÖåÏù¥ÎÑà Î∞∞Í≤Ω ÎçîÏö± Ìà¨Î™ÖÌïòÍ≤å (75% Ìà¨Î™Ö)
  backdrop-filter: blur(35px) saturate(250%); // Í∞ïÌïú Ïú†Î¶¨ Ìö®Í≥º
  -webkit-backdrop-filter: blur(35px) saturate(250%); // Safari ÏßÄÏõê
  border-top: 1px solid rgba(255, 255, 255, 0.1); // Í±∞Ïùò Î≥¥Ïù¥ÏßÄ ÏïäÎäî ÌÖåÎëêÎ¶¨
  padding: 1rem; // spacing-md Í∞í ÏßÅÏ†ë ÏßÄÏ†ï
  z-index: 1000;
  box-shadow: 0 -2px 40px rgba(0, 0, 0, 0.04); // Îß§Ïö∞ Ïó∞Ìïú Í∑∏Î¶ºÏûê
  display: flex;
  justify-content: center;
  align-items: center;
  
  // Î™®Î∞îÏùºÏóêÏÑúÎäî Ï†ÑÏ≤¥ ÎÑàÎπÑ ÏÇ¨Ïö©
  @media (max-width: 767px) {
    left: 0;
    transform: none;
    max-width: none;
  }
}

// ÌîåÎ°úÌåÖ Î≤ÑÌäº ÏûêÏ≤¥ Ïä§ÌÉÄÏùº
.floating-button {
  background-color: #7c746a !important; // Î∏åÎûúÎìú Î∏åÎùºÏö¥ ÏÉâÏÉÅ ÏßÅÏ†ë ÏßÄÏ†ï
  color: #ffffff !important; // Ìù∞ÏÉâ Í∏ÄÏûê
  border: none !important; // ÌÖåÎëêÎ¶¨ Ï†úÍ±∞
  box-shadow: 0 4px 12px rgba(124, 116, 106, 0.3) !important; // Î≤ÑÌäºÏóê Í∑∏Î¶ºÏûê Ï∂îÍ∞Ä
  width: 90% !important; // ÌôîÎ©¥ ÎÑàÎπÑÏùò 90%Î°ú ÌôïÎåÄ
  max-width: 400px !important; // ÏµúÎåÄ ÎÑàÎπÑ Ï†úÌïú
  min-width: 280px !important; // ÏµúÏÜå ÎÑàÎπÑ Î≥¥Ïû•
  padding: 1rem 1.5rem !important; // spacing Í∞í ÏßÅÏ†ë ÏßÄÏ†ï
  font-size: 1rem !important; // font-base Í∞í ÏßÅÏ†ë ÏßÄÏ†ï
  font-weight: 600 !important;
  border-radius: 0.75rem !important; // border-radius-xl Í∞í ÏßÅÏ†ë ÏßÄÏ†ï
  transition: all 0.3s ease !important;
  
  &:hover {
    background-color: #696158 !important; // Ìò∏Î≤ÑÏãú Îçî ÏßÑÌïú Î∏åÎûúÎìú ÏÉâ (Î≥ÄÏàòÍ∞í ÏßÅÏ†ë ÏÇ¨Ïö©)
    transform: translateY(-1px) !important; // ÏÇ¥Ïßù ÏúÑÎ°ú Ïò¨ÎùºÍ∞ÄÎäî Ìö®Í≥º
    box-shadow: 0 6px 16px rgba(139, 69, 19, 0.4) !important; // Ìò∏Î≤ÑÏãú Í∑∏Î¶ºÏûê Í∞ïÌôî
  }
  
  &:active {
    transform: translateY(0) !important; // ÌÅ¥Î¶≠Ïãú ÏõêÎûò ÏúÑÏπòÎ°ú
  }
}

// Í≤∞Í≥º Ìä∏Î†åÎìú Î≤ÑÌäº Ïä§ÌÉÄÏùº
.results-trend-button {
  position: fixed !important;
  bottom: 90px !important; // ÌîåÎ°úÌåÖ Î≤ÑÌäº ÏúÑ
  right: 20px !important;
  padding: 10px 15px !important;
  border-radius: 30px !important;
  font-size: 14px !important;
  background: #6A1B9A !important; // Î≥¥ÎùºÏÉâ Í≥ÑÏó¥
  color: white !important;
  border: none !important;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2) !important;
  z-index: 1000 !important;
  cursor: pointer !important;
  transition: all 0.3s ease !important;
  
  &:hover {
    background: #4A148C !important; // Îçî ÏßÑÌïú Î≥¥ÎùºÏÉâ
    transform: translateY(-2px) !important;
    box-shadow: 0 4px 10px rgba(0,0,0,0.3) !important;
  }
  
  &:active {
    transform: translateY(0) !important;
  }
}

// Î™®Îã¨ Ïä§ÌÉÄÏùº
.modal-overlay {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 50;
  @include flex-center;
  padding: $spacing-md;
  transition: opacity 0.3s ease;
}

.modal-content {
  background: #fdf8e2; // Î≤†Ïù¥ÏßÄ ÌÜ§ Î∞∞Í≤Ω
  width: 100%;
  max-width: 448px; // max-w-md
  border-radius: $border-radius-2xl;
  box-shadow: $shadow-lg;
  padding: $spacing-xl;
  position: relative;
  transform: scale(1);
  transition: transform 0.3s ease;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-close {
  position: absolute;
  top: $spacing-md;
  right: $spacing-md;
  background: none;
  border: none;
  color: $gray-500;
  cursor: pointer;
  padding: $spacing-xs;
  
  &:hover {
    color: $gray-800;
  }
  
  svg {
    width: 24px;
    height: 24px;
  }
}

.modal-title {
  font-size: $font-2xl;
  font-weight: 700;
  color: $brand-brown;
  margin: 0 0 $spacing-sm 0;
  text-align: center;
}

.modal-subtitle {
  font-size: $font-sm;
  color: $gray-600;
  margin: 0 0 $spacing-xl 0;
  text-align: center;
}

.form-fields {
  margin-bottom: $spacing-xl;
  text-align: left;
}

.gender-options {
  display: flex;
  gap: $spacing-md;
}

.gender-option {
  display: flex;
  align-items: center;
  cursor: pointer;
  
  input[type="radio"] {
    margin-right: $spacing-sm;
    accent-color: $brand-brown;
  }
}

// Î™®Îã¨ Í≤∞Í≥º
.modal-result {
  text-align: center;
}

.loader-container {
  @include flex-center;
  margin: $spacing-2xl 0;
}

.loader {
  border: 4px solid #f3f3f3;
  border-top: 4px solid $brand-brown;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.result-text {
  text-align: left;
  font-size: $font-sm;
  background: rgba(255, 255, 255, 0.8);
  padding: $spacing-md;
  border-radius: $border-radius-lg;
  min-height: 150px;
  white-space: pre-wrap;
  margin: $spacing-lg 0;
}

.result-disclaimer {
  font-size: $font-xs;
  color: $gray-500;
  text-align: center;
  margin: $spacing-md 0 0 0;
}

// Î≥ëÏõêÎ™Ö Í∞ïÏ°∞ Ïä§ÌÉÄÏùº
.hospital-name {
  color: $brand-brown; // Í∏∞Ï°¥ Ï†ïÏùòÎêú Î∏åÎûúÎìú Î∏åÎùºÏö¥ ÏÉâÏÉÅ ÏÇ¨Ïö©
  font-size: 1.1em; // Ìè∞Ìä∏ ÌÅ¨Í∏∞ +1 (ÏÉÅÎåÄÏ†ÅÏúºÎ°ú 10% ÌÅ¨Í≤å)
  font-weight: 600; // ÏïΩÍ∞Ñ ÎëêÍªçÍ≤å
}

// Î∞òÏùëÌòï Ï°∞Ï†ï
@include respond-to(md) {
  .main-container {
    margin: 0;
    border-radius: 0;
  }
  
  .header {
    &__content {
      padding: $spacing-lg $spacing-2xl;
    }
  }
  
  .main-content {
    padding: $spacing-md $spacing-xl $spacing-xl;
  }
}
