          padding: 2px; // Îçî ÏûëÏùÄ Ìå®Îî©
          flex-shrink: 0; // ÌÅ¨Í∏∞ Í≥†Ï†ï
          margin-bottom: 1px; // ÎØ∏ÏÑ∏ Ï°∞Ï†ï
          
          .toggle-btn {
