// Í≥†ÎèÑÌôîÎêú Í≤ÄÏßÑ Ïπ¥Îìú Ïä§ÌÉÄÏùº
.advanced-checkup-details {
  .status-badges {
    display: flex;
    gap: $spacing-xs;
    margin-bottom: $spacing-lg;
    flex-wrap: wrap;
    
    .status-badge {
      padding: 4px 8px;
      border-radius: $border-radius-md;
      font-size: $font-sm;
      font-weight: $font-weight-medium;
      border: none;
      cursor: pointer;
      transition: all 0.2s ease;
      
      &:hover {
        transform: translateY(-1px);
        box-shadow: $shadow-sm;
      }
      
      &.active {
        transform: scale(1.05);
        box-shadow: $shadow-md;
      }
      
      &.status-normal {
        background: rgba(139, 69, 19, 0.1); // Í∞àÏÉâ Î∞∞Í≤Ω
        color: $brand-brown; // Í∞àÏÉâ ÌÖçÏä§Ìä∏
        border: 1px solid rgba(139, 69, 19, 0.3); // Í∞àÏÉâ Î≥¥Îçî
        
        &.active {
          background: rgba(139, 69, 19, 0.2);
          border-color: $brand-brown;
        }
      }
      
      &.status-warning {
        background: rgba(245, 158, 11, 0.1);
        color: $warning;
        border: 1px solid rgba(245, 158, 11, 0.3);
        
        &.active {
          background: rgba(245, 158, 11, 0.2);
          border-color: $warning;
        }
      }
      
      &.status-abnormal {
        background: rgba(239, 68, 68, 0.1);
        color: #ef4444;
        border: 1px solid rgba(239, 68, 68, 0.3);
        
        &.active {
          background: rgba(239, 68, 68, 0.2);
          border-color: #ef4444;
        }
      }
    }
  }
  
  .group-slider {
    display: flex;
    gap: 4px;
    margin-bottom: $spacing-lg;
    background: $gray-100;
    border-radius: $border-radius-lg;
    padding: 4px;
    overflow-x: auto;
    
    .group-tab {
      padding: 8px 12px;
      border: none;
      background: transparent;
      border-radius: $border-radius-md;
      font-size: $font-sm;
      font-weight: $font-weight-medium;
      color: $gray-600;
      cursor: pointer;
      transition: all 0.2s ease;
      white-space: nowrap;
      flex-shrink: 0;
      
      &:hover {
        background: rgba(255, 255, 255, 0.5);
        color: $gray-800;
      }
      
      &.active {
        background: $white;
        color: $brand-brown; // Í∞àÏÉâÏúºÎ°ú Î≥ÄÍ≤Ω
        box-shadow: $shadow-sm;
      }
    }
  }
  
  // Í∏∞Ï°¥ ÏÑ∏Î°ú Î†àÏù¥ÏïÑÏõÉ (Î∞±ÏóÖÏö©)
  .group-items {
    display: flex;
    flex-direction: column;
    gap: $spacing-md;
    
    .checkup-item {
      background: rgba(255, 255, 255, 0.8);
      border: 1px solid $gray-200;
      border-radius: $border-radius-lg;
      padding: $spacing-md;
      transition: all 0.2s ease;
      backdrop-filter: blur(5px);
      
      &:hover {
        background: rgba(255, 255, 255, 0.95);
        box-shadow: $shadow-sm;
      }
      
      &.status-abnormal { border-left: 2px solid #ef4444; }
      &.status-warning { border-left: 2px solid $warning; }
      &.status-normal { border-left: 2px solid $brand-brown; } // Í∞àÏÉâÏúºÎ°ú Î≥ÄÍ≤Ω
    }
  }
  
  // Ïä¨ÎùºÏù¥Îçî Ïª®ÌÖåÏù¥ÎÑà
  .slider-container {
    position: relative;
  }

  // ÏÉàÎ°úÏö¥ Í∞ÄÎ°ú Ïä¨ÎùºÏù¥Îçî Î†àÏù¥ÏïÑÏõÉ
  .group-items-slider {
    display: flex;
    gap: $spacing-md;
    overflow-x: auto;
    padding: $spacing-xs 0 $spacing-lg 0;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    
    // Ïä§ÌÅ¨Î°§Î∞î Ïà®Í∏∞Í∏∞ (Îã∑ Ïù∏ÎîîÏºÄÏù¥ÌÑ∞Î°ú ÎåÄÏ≤¥)
    scrollbar-width: none;
    -ms-overflow-style: none;
    &::-webkit-scrollbar {
      display: none;
    }
    
    .checkup-item-card {
      min-width: 200px;
      max-width: 250px;
      flex-shrink: 0;
      background: rgba(255, 255, 255, 0.8);
      border: 1px solid $gray-200;
      border-radius: $border-radius-lg;
      padding: $spacing-md;
      transition: all 0.2s ease;
      backdrop-filter: blur(5px);
      scroll-snap-align: start;
      position: relative; // Î±ÉÏßÄ Ï†àÎåÄ ÏúÑÏπòÎ•º ÏúÑÌïú ÏÉÅÎåÄ ÏúÑÏπò
      
      &:hover {
        background: rgba(255, 255, 255, 0.95);
        box-shadow: $shadow-sm;
        transform: translateY(-2px);
      }
      
      // ÏôºÏ™Ω ÎùºÏù∏ ÏÉâÏÉÅ Ï†úÍ±∞ (ÍπîÎÅîÌïú ÎîîÏûêÏù∏ÏùÑ ÏúÑÌï¥)
      
      // ÏÉÅÌÉú Î±ÉÏßÄ - Ïö∞ÏÉÅÎã® Î∞∞Ïπò
      .status-badge {
        position: absolute;
        top: $spacing-xs;
        right: $spacing-xs;
        padding: 1px 6px; // 2px 8px ‚Üí 1px 6pxÎ°ú Ï∂ïÏÜå
        border-radius: 8px; // 12px ‚Üí 8pxÎ°ú Ï∂ïÏÜå
        font-size: 10px; // $font-xsÎ≥¥Îã§ Îçî ÏûëÍ≤å
        font-weight: 600;
        color: white;
        
        &.status-normal {
          background: $brand-brown; // Í∞àÏÉâÏúºÎ°ú Î≥ÄÍ≤Ω
        }
        
        &.status-warning {
          background: $warning;
        }
        
        &.status-abnormal {
          background: #ef4444;
        }
      }
      
      // Ìï≠Î™©Î™Ö Ïä§ÌÉÄÏùº
      .item-name-only {
        font-size: $font-base;
        font-weight: 600;
        color: $gray-800;
        margin-bottom: $spacing-xs;
        padding-right: 60px; // Î±ÉÏßÄ Í≥µÍ∞Ñ ÌôïÎ≥¥
      }
      
      // ÌÜµÌï© Ïã†Ï≤¥Í≥ÑÏ∏° Ïπ¥Îìú Ïä§ÌÉÄÏùº
      .body-measurements {
        display: flex;
        flex-direction: column;
        gap: $spacing-xs;
        margin-bottom: $spacing-sm;
        
        .measurement-item {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 4px 0;
          
          .measurement-label {
            font-size: $font-base; // $font-smÏóêÏÑú $font-baseÎ°ú Ï¶ùÍ∞Ä (+2)
            color: $gray-600;
            font-weight: 500;
          }
          
          .measurement-value {
            font-size: $font-lg; // $font-baseÏóêÏÑú $font-lgÎ°ú Ï¶ùÍ∞Ä (+2)
            font-weight: 600;
            color: $gray-900;
          }
        }
      }
      
      // Í∏∞Ï°¥ item-header-row Ïä§ÌÉÄÏùº Ï†úÍ±∞ (ÏÉàÎ°úÏö¥ Î†àÏù¥ÏïÑÏõÉÏúºÎ°ú ÎåÄÏ≤¥)
      
      .item-value-large {
        font-size: $font-xl;
        font-weight: $font-weight-bold;
        color: $gray-900;
        margin-bottom: $spacing-sm;
        text-align: center;
        padding: $spacing-xs 0;
      }
      
      .item-references-compact {
        .reference-line {
          display: flex;
          justify-content: space-between;
          align-items: center;
          font-size: $font-sm;
          color: $gray-600;
          margin-bottom: 2px;
          
          &:last-child {
            margin-bottom: 0;
          }
          
          .ref-label {
            font-weight: $font-weight-medium;
            color: $gray-700;
          }
          
          .ref-range {
            color: $gray-500;
            font-size: $font-sm;
          }
        }
      }
    }
  }
}

